
/* font style / format for browser */
@font-face {
    font-family: "Groovy";
    src: url(fonts/ChocoladineDemo.ttf) format("truetype");
  } 


video{
    width: 100%;
  height: auto;
  display: block;
 
}

.hero-box {
    display: grid;
    grid-template-columns: 1fr;
    grid-template-rows: 1fr;
    grid-gap: 0px;
    align-content: center;
    justify-content: center;
    align-items: start;
    height: 100%;
    width: 100%;
  }


.content{
    position:absolute;
    width:  100%;
    height: 100%;
    top: 0;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center; 
}

.hero-text-box {
    grid-column: 1 / -1;
    grid-row: 1 / -1;
    align-self: center;
    text-align: center;
    padding: 1rem;
    z-index: 10;
  }

/* Spaceing/height in between the letters in hero heading */
.hero-heading{

  font-size: 3rem;
  line-height: 3rem;
  letter-spacing: 0.02rem;
  font-weight: bold;
  color: rgb(185, 215, 233);
  font-family: "Groovy", sans-serif;
  text-shadow: 1px 6px 1px rgb(136, 20, 151);
 

}

.hero-text {
    line-height: 1rem;
    letter-spacing: 0.01rem;
    font-size: 1rem;
    margin-top: 0.5rem;
    color: rgb(231, 179, 203);
    text-shadow: 1px 2px 1px rgb(141, 42, 113);
    font-family: Arial, Helvetica, sans-serif;
}

  /* Add Media Queries for Large Screens */

@media (min-width: 600px) {
    .hero-heading {
      font-size: 3rem;
      line-height: 3rem;
    }
    .hero-text {
      padding: 2rem;
     
    }
  }
  
  @media (min-width: 920px) {
    .hero-heading {
      font-size: 4rem;
      line-height: 4rem;
      font-family: "Groovy", sans-serif;
      text-shadow: 4px 6px 1px rgb(136, 20, 151);
    }
}