.i {
  display: flex;
  height: 100vh;
}

.i-left {
  flex: 1;
  display: flex;
  align-items: center;
  justify-content: center;
}
.i-right {
  flex: 1;
  position: relative;
}

.i-left-wrapper {
  padding: 50px;
  height: 50%;
  width: 30%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
/* intro style */
.i-intro {
  font-size: 30px;
  font-weight: 300;
  font-family: Arial, Helvetica, sans-serif;
}
/* name style */
.i-name {
  font-size: 60px;
  line-height: 2.8rem;
  letter-spacing: 0rem;
  font-family: Arial, Helvetica, sans-serif;
}

.i-title {
  height: 50px;
  overflow: hidden;
}

.i-title-wrapper {
  height: 200%;
  animation: move 10s ease-in-out infinite alternate;
}

@keyframes move {
  25% {
    transform: translateY(-50px);
  }
  50% {
    transform: translateY(-100px);
  }
  75% {
    transform: translateY(-150px);
  }
  100% {
    transform: translateY(-200px);
  }
}

/* animation box style */
.i-title-item {
  height: 50px;
  font-size: 30px;
  font-weight: bold;
  color: rgb(173, 76, 76);
  display: flex;
  align-items: center;
  color: rgb(231, 179, 203);
  text-shadow: 1px 2px 1px rgb(141, 42, 113);
  font-family: Arial, Helvetica, sans-serif;
}
/* description style */
.i-desc {
  line-height: 1rem;
  letter-spacing: 0.01rem;
  font-size: 1rem;
  margin-top: 0.5rem;
  color: rgb(231, 179, 203);
  text-shadow: 1px 2px 1px rgb(141, 42, 113);
  font-family: Arial, Helvetica, sans-serif;
}

.i-scroll {
  width: 30px;
  height: 30px;
  position: absolute;
  bottom: 9px;
  float: right;
 
  
}

/* img style */
.i-img {
  width: 90%;
  height: 90%;
  object-fit: cover;
  border-radius: 50%;
}


 @media screen and (max-width: 480px) {
  .i {
    flex-direction: column;
  }

  .i-left-wrapper {
  
    padding: 10px;
    align-items: center;
    justify-content: center;
    text-align: center;
    height: 100%;
  }

  .i-desc{
    display: none;
  }
  
} 
